@import "https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  cursor: none !important;
}

section {
  background: #fff;
  width: 100%;
  min-height: 100vh;
  padding: 100px;
}

section h2 {
  margin-bottom: 10px;
  font-size: 3em;
}

section p {
  margin-bottom: 10px;
  font-size: 1.2em;
}

#cursor {
  mix-blend-mode: difference;
  background: #fff;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  transition: width .5s, height .5s;
  position: fixed;
  transform: translate(-50%, -50%);
}

#cursor:active {
  width: 3200px;
  height: 3200px;
  transition: width 5s, height 5s;
}

#cursor:before {
  content: "";
  opacity: 0;
  background: #000;
  width: 20px;
  height: 2px;
  transition: all .5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#cursor:after {
  content: "";
  height: 20dppx;
  opacity: 0;
  background: #000;
  width: 2px;
  transition: all .5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#cursor:active:before, #cursor:active:after {
  opacity: 1;
}

